@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
.stack-wrapper{width:80vw;display:grid;grid-template-columns:25% 25% 25% 25%;grid-template-rows:200px 200px;justify-items:center;align-items:center;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:15px;row-gap:15px;border:1px solid;margin:35px 15px;padding:20px}@media(max-width: 968px){.stack-wrapper{grid-template-columns:33% 33% 33%;grid-template-rows:200px 200px 200px}}@media(max-width: 668px){.stack-wrapper{width:95vw;grid-template-columns:50% 50%;grid-template-rows:200px 200px 200px 200px}}.stack-hello{font-size:82px;text-align:center;margin:15px auto;max-width:95%;font-family:"Times New Roman",serif}@media(max-width: 768px){.stack-hello{font-size:50px}}.stack-hello::after{content:".";color:#1ee5ff}.MuiBackdrop-root{background-color:rgba(0,0,0,.1) !important}
.stack-tile-wrapper{display:flex;flex-direction:column;align-items:center;justify-content:center;position:relative;margin:10px;border-radius:15px;font-family:"Reggae One",cursive;z-index:0;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}@media(max-width: 468px){.stack-tile-wrapper{max-width:150px;max-height:150px}}.stack-tile-wrapper .stack-tile-svg,.stack-tile-wrapper .stack-tile-img{-webkit-transform:rotateX(90deg);transform:rotateX(90deg);opacity:0;transition:opacity 1s .5s,color 2s 2s,-webkit-transform 1s .5s;transition:transform 1s .5s,opacity 1s .5s,color 2s 2s;transition:transform 1s .5s,opacity 1s .5s,color 2s 2s,-webkit-transform 1s .5s;color:#fff}.stack-tile-wrapper:hover{-webkit-transform:scale(1.1);transform:scale(1.1);z-index:0;transition:all 1s;cursor:pointer}@media(max-width: 768px){.stack-tile-wrapper:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}}.stack-tile-logo{width:auto;border-radius:10px;margin:5px;max-width:100%;max-height:100%;position:relative;z-index:10}.stack-tile-title{width:100%;text-align:center;font-size:24px;line-height:28px;margin:5px;z-index:9;opacity:0;transition:opacity 1s .5s}@media(max-width: 1024px){.stack-tile-title{font-size:20px}}@media(max-width: 768px){.stack-tile-title{font-size:16px}}
.btn{height:56px;width:auto;letter-spacing:1px;font-size:18px;display:inline-flex;align-items:center;background:#002d2d;border:1px solid #69cf9c;border-radius:35px;box-shadow:0 20px 50px rgba(105,207,156,.4);color:#fff;padding:0 40px;font:700 16px/1 "Shopify Sans",Arial,sans-serif;position:relative;z-index:9}@media(max-width: 868px){.btn{height:46px}}.btn::before{content:"";display:block;position:absolute;background:linear-gradient(180deg, #002d2d, #69cf9c);width:100%;height:100%;left:0;border-radius:35px;z-index:-1;opacity:.45;-webkit-animation:btnAnimation 6s infinite;animation:btnAnimation 6s infinite}.btn:hover{cursor:pointer;background:linear-gradient(180deg, #002d2d, #69cf9c)}
